@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@800&display=swap");

html {
  height: 100%;
}
body {
  background-color: hsl(218, 23%, 16%);
  height: 100%;
  font-family: "Manrope", sans-serif;
}
main {
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
}
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: hsl(217, 19%, 24%);
  padding: 20px 30px;
  position: relative;
  border-radius: 10px;
  width: 500px;
}
.App h5 {
  color: hsl(150, 100%, 66%);

  text-align: center;
  letter-spacing: 4px;
}

.App p {
  color: hsl(193, 38%, 86%);
  text-align: center;
  font-size: 28px;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.App p img {
  margin-top: 30px;
}

.App .circularButton {
  cursor: pointer;
  display: flex;
  align-items: center;
  background-color: hsl(150, 100%, 66%);
  width: 2.5rem;
  height: 2.5rem;
  padding: 0.3rem;
  border-radius: 50%;
  position: absolute;
  bottom: -1.3rem;
  justify-content: center;
}

.App .circularButton:hover {
  box-shadow: 0 0 20px 5px hsl(150, 100%, 66%);
  transition: box-shadow 200ms ease-in-out;
}

.App .circularButton img {
  width: 20px;
}

@media (max-width: 600px) {
  .App {
    width: 80%;
  }
}
